.ContactMe {
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 2cm;
  position: relative;
  overflow: auto;
  min-height: 100vh;
}

.contact-photo {
  width: 8cm;
  height: 8cm;
}

.contactMeImg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
}

.contact-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  column-gap: 0.5cm;
  row-gap: 0.5cm;
  flex-wrap: wrap;
  width: fit-content;
}

.contact {
  border: thin solid antiquewhite;
  box-shadow: rgba(0, 0, 0, 0.5) 0px 5px 12px;
  padding: 15px;
  height: fit-content;
  width: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 0.2cm;
  border-radius: 15px;
}

.contact:hover {
  transform: scale(1.1);
  transition: 0.5s;
}

.contactImg {
  height: 1.5cm;
  width: 1.5cm;

}

@media only screen and (max-width : 700px) {
  .ContactMe {
    flex-direction: column;
  }
}

@media only screen and (max-width:350px) {
  .contact-photo {
    width: 5cm;
    height: 5cm;
  }

  .contactImg {
    height: 1cm;
    width: 1cm;
  }
}